"use client";
import { useAuth } from "@/context/AuthProvider";
import { useRouter } from "next/navigation";

import api from "@/services/backend-api/authApi";

const UserProfile: React.FC = () => {
  const { user, isAuthenticated } = useAuth();
  const router = useRouter();

  const handleLogout = () => {
    (async () => {
      try {
        const response = await api.logout();
        console.log(`${response.data}`);
      } catch (error) {
        console.error(error);
      }
    })();
  };

  if (!isAuthenticated) {
    return null;
  }

  return (
    <div className="flex items-center p-4 bg-red-200">
      <img
        src={user?.avatar || "/default-avatar.png"} // Показываем аватар или placeholder
        alt="User Avatar"
        className="w-32 h-32 rounded-full object-cover"
      />
      <div>
        <h2 className="text-xl font-semibold text-gray-800">
          {user?.first_name} {user?.last_name}
        </h2>
        <p className="text-gray-600">{user?.email}</p>
      </div>
      <button
        className="bg-blue-500 px-5 py-2 rounded-lg"
        onClick={() => router.push("/auth/profile/update/")}
      >
        Edit
      </button>
      <button
        className="bg-red-500 text-white font-bold px-5 py-2 rounded-lg"
        onClick={handleLogout}
      >
        Logout
      </button>
    </div>
  );
};

export default UserProfile;
